<template>
    <div class="rights">
        <!-- 路径导航 -->
        <el-row>
        <el-col :span="24">
            <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>权限管理</el-breadcrumb-item>
            <el-breadcrumb-item>权限列表</el-breadcrumb-item>
            </el-breadcrumb>
        </el-col>
        </el-row>
         <!-- 用户数据列表区域 -->
    <el-table
      :data="rightsList"
      border
      style="width: 100%">
      <el-table-column
      type="index"
      width="50"
      v-loading="loading">
      </el-table-column>
        <el-table-column
          prop="authName"
          label="权限名称"
          width="280">
        </el-table-column>
        <el-table-column
          prop="path"
          label="路径"
          width="280">
      </el-table-column>
      <el-table-column label="层级路径">
        <template slot-scope="scope">
            <!-- 这里的level值为数字，但是在页面中我们需要显示的不是数字，所以这里需要使用到过滤器，而如果像之前的列使用 el-table-column prop属性来实现的话那么就无法使用过滤器了，因为prop无法添加过滤器，所以这进而使用插值表达式。-->
            <span>{{scope.row.level | formatLevel}}</span>
        </template>
      </el-table-column>
    </el-table>
    </div>
</template>
<script>
import {getRightsList} from '@/api/index.js'
export default {
  data () {
    return {
      loading: true,
      rightsList: []
    }
  },
  // 添加过滤器，将level数字转换为字符串
  filters: {
    formatLevel (level) {
      if (level === '0') {
        return '一级'
      } else if (level === '0') {
        return '二级'
      } else {
        return '三级'
      }
    }
  },
  created () {
    this.loading = true
    getRightsList({type: 'list'})
      .then(res => {
        // console.log(res)
        if (res.meta.status === 200) {
          this.rightsList = res.data
          this.loading = false
        }
      })
  }
}
</script>
<style lang="scss" scoped>

</style>



