<template>
  <div class="user">
    <!-- 路径导航 -->
    <el-row>
      <el-col :span="24">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>用户管理</el-breadcrumb-item>
          <el-breadcrumb-item>用户列表</el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
    </el-row>
    <!-- 用户数据搜索区域 -->
    <el-row>
      <el-col :span="24">
        <!-- @keydown.enter="getList":这种方式加人原生的button等才有用。。对于el-input无用 -->
        <!-- el-input不是一个标签，而是一个组件。如果想为组件添加事件，那么需要使用到.native修饰符 -->
        <el-input placeholder="请输入内容" class="search-input" v-model="queryString" @keydown.native.enter="getList">
          <el-button slot="append" icon="el-icon-search" @click='getList'></el-button>
        </el-input>
        <el-button type="success" plain @click='addDialogFormVisible=true'>添加用户</el-button>
      </el-col>
    </el-row>
    <!-- 用户数据列表区域 -->
    <el-table
      :data="userList"
      border
      style="width: 100%">
      <el-table-column
      type="index"
      width="50"
      v-loading="loading">
      </el-table-column>
        <el-table-column
          prop="username"
          label="用户名"
          width="180">
        </el-table-column>
        <el-table-column
          prop="email"
          label="邮箱"
          width="280">
        </el-table-column>
        <el-table-column
          prop="mobile"
          label="电话"
          width="280">
      </el-table-column>
      <el-table-column label="用户状态">
        <template slot-scope="scope">
          <!-- 这里不能通过prop来进行绑定了，因为之前是直接绑定给元素，因为现在是使用模板进行处理的 -->
          <el-switch
            v-model="scope.row.mg_state"
            active-color="#13ce66"
            inactive-color="#ff4949"
            @change='changeState(scope.row)'>
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button  type="primary" icon="el-icon-edit" size="mini" plain @click="showEditUserDialog(scope.row)"></el-button>
          <el-button  type="warning" icon="el-icon-delete" size="mini" plain @click="showDeleteBox(scope.row.id)"></el-button>
          <el-button  type="danger" icon="el-icon-check" size="mini" plain @click="showGrantUserBox(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页区域 -->
    <div class="page">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="3"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <!-- 添加用户对话框 -->
    <!-- :visible.sync: addDialogFormVisible:设置当前对话框是显示还是隐藏-->
    <el-dialog title="添加用户" :visible.sync="addDialogFormVisible">
      <el-form :model="addform"  label-width="80px" :rules="rules" ref='addform'>
        <!-- prop:设置所需要校验的字段，必须设置，否则校验规则不起作用 -->
        <el-form-item label="用户名" prop='username'>
          <el-input v-model="addform.username" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码" prop='password'>
          <el-input v-model="addform.password" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop='email'>
          <el-input v-model="addform.email" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop='mobile'>
          <el-input v-model="addform.mobile" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetFor('addform')">取 消</el-button>
        <el-button type="primary" @click="addUser('addform')">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 修改用户对话框 -->
    <el-dialog title="添加用户" :visible.sync="editDialogFormVisible">
      <el-form :model="editform"  label-width="80px" :rules="rules" ref='editform'>
        <!-- prop:设置所需要校验的字段，必须设置，否则校验规则不起作用 -->
        <el-form-item label="用户名" prop='username'>
          <el-input v-model="editform.username" auto-complete="off" disabled></el-input>
        </el-form-item>
        <el-form-item label="邮箱" prop='email'>
          <el-input v-model="editform.email" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="电话" prop='mobile'>
          <el-input v-model="editform.mobile" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetFor('editform')">取 消</el-button>
        <el-button type="primary" @click="editUser('editform')">确 定</el-button>
      </div>
    </el-dialog>

    <!-- 分配用户角色 -->
    <el-dialog title="分配角色" :visible.sync="grantDialogFormVisible">
      <el-form :model="grantform"  label-width="120px">
        <!-- prop:设置所需要校验的字段，必须设置，否则校验规则不起作用 -->
        <el-form-item label="当前用户：" prop='username'>
          <el-tag type="info">{{grantform.username}}</el-tag>
        </el-form-item>
         <el-form-item label="请选择角色：">
           <!-- v-model="":当用户进行选择之后，会将当前所选择数据对应的id与v-model进行绑定 -->
          <el-select v-model="roleId" placeholder="请选择角色">
            <!-- :label:这个值是变化的，所以使用动态绑定 -->
            <el-option :label="role.roleName" :value="role.id" v-for="(role,index) in roleList" :key="index"  ></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetFor('editform')">取 消</el-button>
        <el-button type="primary" @click="grantUser()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import {getUserList, changeUserState, addUser, editUserInfo, deleteUser, getRoleList, grantUserRole} from '@/api/index.js'
export default {
  data () {
    return {
      userList: [],
      queryString: '',
      total: 0,
      pagesize: 3,
      pagenum: 1,
      loading: true,
      addDialogFormVisible: false,
      editDialogFormVisible: false,
      grantDialogFormVisible: false,
      addform: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      editform: {
        id: 0,
        username: '',
        email: '',
        mobile: ''
      },
      grantform: {
        username: '',
        id: ''
      },
      roleList: [],
      roleId: '',
      rules: {
        username: [
          {required: true, message: '请输入用户名', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '请输入密码', trigger: 'blur'}
        ],
        email: [
          // 第一个规则是让用户一定要输入邮箱
          {required: true, message: '请输入邮箱', trigger: 'blur'},
          // 第二个校验规则是如果用户输入了内容，内容必须输入正确合法
          // type: 'email':说明只能输入邮箱类型
          {type: 'email', message: '请输入正确的邮箱地址', trigger: 'blur,change'}
        ],
        mobile: [
          {required: true, message: '请输入手机号', trigger: 'blur'}
        ]
      }
    }
  },
  created () {
    this.getList()
  },
  methods: {
    // 显示分配角色对话框
    showGrantUserBox (row) {
      this.grantform.username = row.username
      this.grantform.id = row.id
      getRoleList()
        .then(res => {
          if (res.meta.status === 200) {
            this.roleList = res.data
          }
        })
      this.grantDialogFormVisible = true
    },
    // 分配角色
    grantUser () {
      console.log({id: this.grantform.id, rid: this.roleId})
      // 判断用户是否已经选择了角色，如果没有选择则不进行本次的请求处理
      if (!this.roleId) {
        this.$message({
          message: '请选择角色',
          type: 'warining'
        })
      } else {
        grantUserRole({id: this.grantform.id, rid: this.roleId})
          .then(res => {
            if (res.meta.status === 200) {
            // 显示提示信息
              this.$message({
                message: '分配角色成功',
                type: 'success'
              })
              // 隐藏用户用户dialog
              this.grantDialogFormVisible = false
            }
          })
      }
    },
    // 显示删除对话框
    showDeleteBox (id) {
      this.$confirm('此操作将永久删除该记录, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteUser(id).then(res => {
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.getList()
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        })
      })
    },
    // 实现编辑操作
    editUser (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          editUserInfo(this.editform)
            .then(res => {
              console.log(res)
              if (res.meta.status === 200) {
                // 显示提示信息
                this.$message({
                  message: '编辑用户成功',
                  type: 'success'
                })
                // 隐藏用户用户dialog
                this.editDialogFormVisible = false
                // 数据刷新
                this.getList()
                // 清除之前表单数据
                this.$refs[formName].resetFields()
              }
            })
        } else {
          alert('123')
          return false
        }
      })
    },
    // 显示编辑用户对话框
    showEditUserDialog (row) {
      this.editDialogFormVisible = true
      this.editform.username = row.username
      this.editform.email = row.email
      this.editform.mobile = row.mobile
      this.editform.id = row.id
      console.log('------')
      console.log(row)
      console.log('------')
    },
    // 添加用户时的数据验证
    addUser (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          addUser(this.addform)
            .then(res => {
              console.log(res)
              if (res.meta.status === 201) {
                // 显示提示信息
                this.$message({
                  message: '创建用户成功',
                  type: 'success'
                })
                // 隐藏用户用户dialog
                this.addDialogFormVisible = false
                // 数据刷新
                this.getList()
                // 清除之前表单数据
                this.$refs[formName].resetFields()
              }
            })
        } else {
          alert('123')
          return false
        }
      })
    },
    resetFor (formName) {
      this.$refs[formName].resetFields()
      this.addDialogFormVisible = false
      this.editDialogFormVisible = false
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
      this.pagesize = val
      this.getList()
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      this.pagenum = val
      this.getList()
    },
    handleEdit (index, row) {
      console.log(index, row)
    },
    handleDelete (index, row) {
      console.log(index, row)
    },
    changeState (row) {
      console.log(row)
      changeUserState({uid: row.id, type: row.mg_state})
        .then(res => {
          console.log(res)
          if (res.meta.status === 200) {
            this.$message({
              message: '修改用户状态成功',
              type: 'success'
            })
          } else {
            this.$message({
              message: res.meta.msg,
              type: 'warning'
            })
          }
        })
    },
    // 初始化表格数据
    getList () {
      this.loading = true
      getUserList({params: {query: this.queryString, pagenum: this.pagenum, pagesize: this.pagesize}})
        .then(res => {
          console.log(res)
          this.userList = res.data.users
          this.total = res.data.total
          // 清除加载动画效果
          this.loading = false
        })
    }
  }
}
</script>
<style lang="scss" scoped>
.user {
  .search-input {
    width: 300px;
  }
  .page {
    background-color: #D3DCE6;
    padding: 5px 0;
    margin-top: 10px;
  }
}
</style>



