<template>
    <div class="home">
        <el-container>
            <!--router:true  是否使用 vue-router 的模式，启用该模式会在激活导航时以 index 作为 path 进行路由跳转 -->
            <el-aside width="auto">
                <div class="logo"></div>
                <el-menu
                class="el-menu-vertical-demo el-menu-admin"
                @open="handleOpen"
                @close="handleClose"
                :router="true"
                :collapse="isCollapse"
                background-color="#545c64"
                :unique-opened='true'
                text-color="#fff"
                active-text-color="#ffd04b">
                    <el-submenu :index="item.path" v-for="item in menuList" :key="item.id">
                        <template slot="title">
                            <i class="el-icon-location"></i>
                            <span>{{item.authName}}</span>
                        </template>
                        <el-menu-item :index="sub.path" v-for="sub in item.children" :key="sub.id">
                            <i class="el-icon-menu"></i>
                            <span slot="title">{{sub.authName}}</span>
                        </el-menu-item>
                    </el-submenu>
                </el-menu>
            </el-aside>
            <el-container>
                <el-header>
                    <span class="myicon myicon-menu toggle-btn" @click="toggleCollapse"></span>
                    <div class="system-title">电商后台管理系统</div>
                    <div>
                        <div class="welcome">
                            <!-- <span>你好：{{$store.state.username}}</span> -->
                            <span>你好：{{$store.getters.currentUserName}}</span>
                            <el-button type="text" @click="loginOut">退出</el-button>
                        </div>
                    </div>
                </el-header>
                <el-main>
                    <!-- 核心内容部分 -->
                    <router-view></router-view>
                </el-main>
            </el-container>
        </el-container>
    </div>
</template>
<script>
import { getUserList, getLeftMenu } from '@/api/index.js'
export default {
  data () {
    return {
      isCollapse: false,
      menuList: []
    }
  },
  created () {
    getLeftMenu()
      .then(res => {
        // console.log(res)
        // 判断请求是否成功
        this.menuList = res.data
      })
  },
  methods: {
    handleOpen (key, keyPath) {
      console.log(key, keyPath)
    },
    handleClose (key, keyPath) {
      console.log(key, keyPath)
    },
    toggleCollapse () {
      this.isCollapse = !this.isCollapse
    },
    loginOut () {
      // 清空localStorage中的数据--重置创建状态
      localStorage.removeItem('loginToken')
      // 跳转回登陆页面
      this.$router.push({name: 'Login'})
    }
  },
  mounted () {
    let params = { params: { query: '', pagenum: 1, pagesize: 10 } }
    getUserList(params).then(result => {
    })
  }
}
</script>
<style lang='scss' scoped>
    .home {
        height: 100%;
        .el-menu-admin:not(.el-menu--collapse) {
            width: 200px;
            min-height: 400px;
        }
        .el-container {
            height: 100%;
        }
        .el-aside {
            background-color: rgb(84, 92, 100);
        }
        .el-header {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background-color: rgb(84, 92, 100);
        }
        .logo {
            height: 60px;
            background: url(../assets/logo.png);
            background-size: cover;
            background-color: white;
        }
        .toggle-btn {
            padding: 0 15px;
            margin-left: -20px;
            font-size: 36px;
            color: white;
            cursor: pointer;
            line-height: 60px;
            &:hover {
                background-color: #4292cf;
            }
        }
        .system-title {
            font-size: 28px;
            color: white;
        }
        .welcome {
            color: white;
        }
    }
</style>



