import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/views/Login.vue'
import Home from '@/views/Home.vue'
import Welcome from '@/views/welcome/welcome.vue'
import User from '@/views/user/User.vue'
import Rights from '@/views/rights/Rights.vue'
import Roles from '@/views/rights/Roles.vue'
import Category from '@/views/category/Category.vue'
import AddGoods from '@/views/goods/AddGoods.vue'
import Goods from '@/views/goods/Goods.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/',
      name: 'Home',
      component: Home,
      // 配置路由重定向：当路由为/的时候，重定向到/welcome
      redirect: {path: 'welcome'},
      children: [
        {name: 'Welcome', path: 'welcome', component: Welcome},
        {name: 'Users', path: 'users', component: User},
        {name: 'Rights', path: 'rights', component: Rights},
        {name: 'Roles', path: 'roles', component: Roles},
        {name: 'Category', path: 'categories', component: Category},
        {name: 'Goods', path: 'goods', component: Goods},
        {name: 'AddGoods', path: 'toadd', component: AddGoods}
      ]
    }
  ]
})



