<template>
  <div class="category">
    <!-- 路径导航 -->
    <el-row>
      <el-col :span="24">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>商品管理</el-breadcrumb-item>
          <el-breadcrumb-item>分类分类</el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
    </el-row>
    <!-- 用户数据搜索区域 -->
    <el-row>
      <el-col :span="24">
        <el-button type="success" plain @click='addCateDialog'>添加分类</el-button>
      </el-col>
    </el-row>
    <tree-grid
      :treeStructure="true"
      :columns="columns"
      :data-source="dataSource"
      @deleteCate="deleteCategory"
      @editCate="editCategory"
    >
    </tree-grid>
    <!-- 分页区域 -->
    <div class="page">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="1"
        :page-sizes="[5, 10, 15, 20]"
        :page-size="5"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
      </el-pagination>
    </div>
    <!-- 添加分类对话框 -->
    <el-dialog title="添加分类" :visible.sync="addDialogFormVisible">
      <el-form :model="addform"  label-width="80px" :rules="rules" ref='addform'>
        <!-- prop:设置所需要校验的字段，必须设置，否则校验规则不起作用 -->
        <el-form-item label="分类名称" prop='cat_name'>
          <el-input v-model="addform.cat_name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="父级名称">
          <el-cascader
            :options="options"
            v-model="selectedOptions"
            @change="handleChange"
            :props="props"
            >
          </el-cascader>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="resetFor('addform')">取 消</el-button>
        <el-button type="primary" @click="addcategory('addform')">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
// 引入组件
import TreeGrid from '@/components/TreeGrid/TreeGrid.vue'
import {getCategory, addCategories} from '@/api/index.js'
export default {
  data () {
    return {
      addform: {
        cat_name: 'aa',
        // 分类父ID
        cat_pid: 0,
        // 分类层级
        cat_level: 0
      },
      addDialogFormVisible: false,
      columns: [{
        text: '分类名称',
        dataIndex: 'cat_name',
        width: ''
      }, {
        text: '是否有效',
        dataIndex: 'cat_deleted',
        width: ''
      }, {
        text: '排序',
        dataIndex: 'cat_level',
        width: ''
      }],
      dataSource: [],
      // 级联选择器的数据源
      options: [],
      // 级联选择器选中后的数据
      selectedOptions: [],
      // props 表示配置级联选择器所展示的数据字段
      props: {
        value: 'cat_id',
        label: 'cat_name'
      },
      total: 0,
      pagesize: 5,
      pagenum: 1,
      rules: {
        categoryname: [
          {required: true, message: '请输入分类名称', trigger: 'blur'}
        ]
      }
    }
  },
  created () {
    this.initList()
  },
  methods: {
    //   实现添加分类
    addcategory (formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          // 判断用户有没有选择分类
          if (this.selectedOptions.length === 0) {
            this.addform.cat_pid = 0
            this.addform.cat_level = 0
          } else if (this.selectedOptions.length === 1) {
            this.addform.cat_pid = this.selectedOptions[0]
            this.addform.cat_level = 1
          } else {
            this.addform.cat_pid = this.selectedOptions[1]
            this.addform.cat_level = 2
          }
          addCategories(this.addform)
            .then(res => {
              console.log(res)
              if (res.meta.status === 201) {
                this.addDialogFormVisible = false
                this.initList()
                this.$message({
                  type: 'success',
                  message: res.meta.msg
                })
              }
            })
        }
      })
    },
    //   打开添加分类对话框， 同时加载默认数据
    addCateDialog () {
      this.addDialogFormVisible = true
      getCategory({type: '2'})
        .then(res => {
          console.log(res.data)
          if (res.meta.status === 200) {
            this.options = res.data
          }
        })
    },
    //   级联选择事件
    handleChange (value) {
      console.log(value)
    },
    //   初始化数据
    initList () {
      getCategory({pagesize: this.pagesize, pagenum: this.pagenum, type: '3'})
        .then(res => {
          if (res.meta.status === 200) {
            this.total = res.data.total
            this.dataSource = res.data.result
          }
        })
    },
    deleteCategory (cid) {
      console.log(cid)
    },
    editCategory (cid) {
      console.log(cid)
    },
    handleSizeChange (val) {
      console.log(`每页 ${val} 条`)
      this.pagesize = val
      this.initList()
    },
    handleCurrentChange (val) {
      console.log(`当前页: ${val}`)
      this.pagenum = val
      this.initList()
    }
  },
  // 注入引入的组件
  components: {
    TreeGrid
  }
}
</script>
<style lang="scss" scoped>
  .page {
    background-color: #D3DCE6;
    padding: 5px 0;
    margin-top: 10px;
  }
</style>



