<template>
    <div class="goods">
        <!-- 路径导航 -->
        <el-row>
        <el-col :span="24">
            <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>商品列表</el-breadcrumb-item>
            </el-breadcrumb>
        </el-col>
        </el-row>
         <!-- 用户数据搜索区域 -->
        <el-row>
        <el-col :span="24">
            <!-- @keydown.enter="getList":这种方式加人原生的button等才有用。。对于el-input无用 -->
            <!-- el-input不是一个标签，而是一个组件。如果想为组件添加事件，那么需要使用到.native修饰符 -->
            <el-input placeholder="请输入内容" class="search-input" v-model="queryString">
            <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
            <el-button type="success" plain @click='addGoods'>添加用户</el-button>
        </el-col>
        </el-row>
    </div>
</template>
<script>
export default {
  data () {
    return {
      queryString: ''
    }
  },
  methods: {
    addGoods () {
      this.$router.push({'name': 'AddGoods'})
    }
  }
}
</script>
<style lang="scss" scoped>
.search-input{
    width: 300px;
}
</style>



