import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)
const state = {
  username: ''
}
// mutations用来操作state中的数据
const mutations = {
  setUserName: (state, username) => {
    state.username = username
    // vuex中保存的数据是应用级别的，当页面刷新的时候，整个页面会被重新加载，store中保存的数据会被还原，所以这里可以使用localStorage再次进行数据的存储，在需要使用的时候再从localStorage中读取
    localStorage.setItem('username', username)
  }
}
// actions可以触发mutations中的方法
const actions = {
  setUserNameAction: ({ commit }, obj) => commit('setUserName', obj.username)
}
const getters = {
  currentUserName: (state) => localStorage.getItem('username') === '' ? localStorage.getItem('username') : localStorage.getItem('username')
}
export default new Vuex.Store({
  state,
  getters,
  actions,
  mutations
})



