<template>
    <div class="goods">
        <!-- 路径导航 -->
        <el-row>
        <el-col :span="24">
            <el-breadcrumb separator="/">
            <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
            <el-breadcrumb-item>商品管理</el-breadcrumb-item>
            <el-breadcrumb-item>添加商品</el-breadcrumb-item>
            </el-breadcrumb>
        </el-col>
        </el-row>
        <!-- 步骤条 -->
        <el-steps :active="active" finish-status="success">
            <el-step title="步骤 1"></el-step>
            <el-step title="步骤 2"></el-step>
            <el-step title="步骤 3"></el-step>
            <el-step title="步骤 4"></el-step>
            <el-step title="步骤 5"></el-step>
        </el-steps>
        <!-- 标签页 -->
        <el-tabs
          v-model="activeName"
          @tab-click="handleClick"
          tab-position='left'
          style="margin-top:20px"
        >
            <el-tab-pane label="基本信息" name="first">基本信息</el-tab-pane>
            <el-tab-pane label="商品参数" name="second">商品参数</el-tab-pane>
            <el-tab-pane label="商品属性" name="third">商品属性</el-tab-pane>
            <el-tab-pane label="商品图片" name="fourth">
                <!-- action:必选参数，上传的地址 -->
                <el-upload
                    action="http://localhost:8888/api/private/v1/upload"
                    :on-preview="handlePreview"
                    :on-remove="handleRemove"
                    :headers='setHeaderOfToken()'
                    :on-success="handleSuccess"
                    :multiple="true"
                    list-type="picture">
                    <el-button size="small" type="primary">点击上传</el-button>
                </el-upload>
            </el-tab-pane>
            <el-tab-pane label="商品内容" name="fifth">商品内容</el-tab-pane>
        </el-tabs>
    </div>
</template>
<script>
export default {
  data () {
    return {
      // 设置active属性，接受一个Number，表明步骤的 index，从 0 开始
      active: 0,
      activeName: 'first'
    }
  },
  methods: {
    //   图片上传成功之后的钩子函数
    handleSuccess (response, file, fileList) {
      console.log(fileList)
      if (response.meta.status === 200) {
        this.$message({
          type: 'success',
          message: response.meta.msg
        })
      }
    },
    // 设置上传图片时需要的token
    setHeaderOfToken () {
      let token = localStorage.getItem('loginToken')
      return {'Authorization': token}
    },
    next () {
      if (this.active++ > 4) this.active = 0
    },
    handleClick (tab, event) {
      console.log(tab, event)
      // tab.name:可以获取当前标签页导航项的名称
      switch (tab.name) {
        case 'first':
          this.active = 0
          break
        case 'second':
          this.active = 1
          break
        case 'third':
          this.active = 2
          break
        case 'fourth':
          this.active = 3
          break
        case 'fifth':
          this.active = 4
          break
      }
    },
    // 实现预览
    handlePreview () {

    },
    // 实现删除
    handleRemove () {

    }
  }
}
</script>
<style lang="scss" scoped>

</style>



