import axios from 'axios'
const baseURL = 'http://localhost:8888/api/private/v1/'
axios.defaults.baseURL = baseURL

// 添加请求拦截器
axios.interceptors.request.use(function (config) {
  // Do something before request is sent
  // 将token 添加到一个前后台约定好的key中，做为请求头发送
  let itcastToken = localStorage.getItem('loginToken')
  if (itcastToken) {
    config.headers['Authorization'] = itcastToken
  }
  return config
}, function (error) {
  // Do something with request error
  return Promise.reject(error)
})

// 定义一个方法，实现登陆验证
export const checkLogin = (pa) => {
  // console.log(pa)
  return axios.post('login', pa)
    .then((result) => {
      // console.log(result.data)
      return result.data
    })
}
// 获取用户列表
export const getUserList = (pa) => {
  return axios.get('users', pa)
    .then((result) => {
      return result.data
    })
}
// 更改用户状态
export const changeUserState = (pa) => {
  return axios.put(`users/${pa.uid}/state/${pa.type}`)
    .then(res => {
      return res.data
    })
}
// 添加用户
export const addUser = (pa) => {
  return axios.post('users', pa)
    .then(res => {
      return res.data
    })
}
// 根据id获取用户信息
export const editUserInfo = (pa) => {
  return axios.put(`users/${pa.id}`, pa)
    .then(res => {
      return res.data
    })
}
// 根据id删除用户
export const deleteUser = (pa) => {
  return axios.delete(`users/${pa}`)
    .then(res => {
      return res.data
    })
}
// 获取角色列表
export const getRoleList = () => {
  return axios.get('roles')
    .then(res => {
      return res.data
    })
}
// 分配角色
export const grantUserRole = (pa) => {
  return axios.put(`users/${pa.id}/role`, {id: pa.id, rid: pa.rid})
    .then(res => {
      return res.data
    })
}
// 获取所有权限列表
export const getRightsList = (pa) => {
  return axios.get(`rights/${pa.type}`)
    .then(res => {
      return res.data
    })
}
// 删除用户指定角色
export const deleteRight = (pa) => {
  console.log(pa)
  return axios.delete(`roles/${pa.roleId}/rights/${pa.rightId}`)
    .then(res => {
      return res.data
    })
}
// 角色授权
export const grantRoleRight = (roleId, rids) => {
  console.log(roleId + ':' + rids)
  return axios.post(`roles/${roleId}/rights`, rids)
    .then(res => {
      console.log(res.data)
      return res.data
    })
}
// 获取左侧菜单权限
export const getLeftMenu = () => {
  return axios.get('menus')
    .then(res => {
      return res.data
    })
}
// 获取商品数据列表
export const getCategory = (pa) => {
  return axios.get('categories', {params: pa})
    .then(res => {
      return res.data
    })
}
// 添加分类
export const addCategories = (pa) => {
  return axios.post('categories', pa)
    .then(res => {
      return res.data
    })
}



