<template>
    <div class="roles">
        <!-- 路径导航 -->
    <el-row>
      <el-col :span="24">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>权限管理</el-breadcrumb-item>
          <el-breadcrumb-item>角色列表</el-breadcrumb-item>
        </el-breadcrumb>
      </el-col>
    </el-row>
    <!-- 用户数据搜索区域 -->
    <el-row>
      <el-col :span="24">
        <el-button type="success" plain>添加角色</el-button>
      </el-col>
    </el-row>
        <!-- 角色数据列表区域 -->
        <!-- 通过设置 type="expand" 和 Scoped slot 可以开启展开行功能，el-table-column 的模板会被渲染成为展开行的内容，展开行可访问的属性与使用自定义列模板时的 Scoped slot 相同。 -->
        <el-table
            :data="roleList"
            border
            style="width: 100%; margin-top:15px">
            <el-table-column type="expand">
                <template slot-scope="scope">
                    <el-row v-for="firstLevel in scope.row.children" :key="firstLevel.id" style="margin-top:5px">
                        <el-col :span="4">
                            <!-- close:关闭Tag时触发的事件 -->
                            <el-tag closable @close="deleteRoleRight(scope.row,firstLevel.id)">
                            {{firstLevel.authName}}
                            </el-tag>
                            <span class="el-icon-arrow-right" v-if="firstLevel.children.length !== 0"></span>
                        </el-col>
                        <el-col :span="20">
                            <!-- 嵌套el-row -->
                            <el-row v-for="secondLevel in firstLevel.children" :key="secondLevel.id">
                                <el-col :span="3">
                                    <el-tag closable type="success" @close="deleteRoleRight(scope.row,secondLevel.id)">
                                    {{secondLevel.authName}}
                                    </el-tag>
                                    <span class="el-icon-arrow-right" v-if="secondLevel.children.length !== 0"></span>
                                </el-col>
                                <el-col :span="21">
                                    <el-tag closable type="danger" v-for="thirdLevel in secondLevel.children" :key="thirdLevel.id" @close="deleteRoleRight(scope.row,thirdLevel.id)">
                                    {{thirdLevel.authName}}
                                    </el-tag>
                                </el-col>
                            </el-row>
                        </el-col>
                    </el-row>
                    <el-row>
                        <el-col :span="24" v-if="scope.row.children.length == 0">该角色还没有分配权限，请前往分配</el-col>
                    </el-row>
                </template>
            </el-table-column>
            <el-table-column label="角色名称" prop="roleName" width="180px">
            </el-table-column>
            <el-table-column label="描述" prop="roleDesc"  width="110px">
            </el-table-column>
            <el-table-column label="操作">
                <template slot-scope="scope">
                    <el-button  type="primary" icon="el-icon-edit" size="mini" plain title='编辑'></el-button>
                    <el-button  type="warning" icon="el-icon-delete" size="mini" plain title='删除'></el-button>
                    <el-button  type="danger" icon="el-icon-check" size="mini" plain @click="showGrantDialog(scope.row)" title='授权角色'></el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分配角色dialog -->
        <el-dialog title="授权角色" :visible.sync="dialogFormVisible">
            <div class="roleContainer">
                <el-tree
                :data="rolesList"
                show-checkbox
                :accordion="true"
                node-key="id"
                ref="tree"
                :default-expand-all="true"
                :default-checked-keys="selectedRightId"
                :props="defaultProps">
            </el-tree>
            </div>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="grantRoleSubmit">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>
<script>
import {getRoleList, deleteRight, getRightsList, grantRoleRight} from '@/api/index.js'
export default {
  data () {
    return {
      roleList: [],
      dialogFormVisible: false,
      rolesList: [],
      // 当前行
      currentRow: {},
      defaultProps: {
        // children：  需要展示的字段属性名称，前面的属性名称是固定的，后面的值是根据数据进行设置
        // label:设置所展示的内容
        children: 'children',
        label: 'authName'
      },
      // 保持默认被选择的权限id
      selectedRightId: []
    }
  },
  created () {
    this.initList()
  },
  methods: {
    initList () {
      getRoleList().then(res => {
        // console.log(res)
        if (res.meta.status === 200) {
        //   console.log(res)
          this.roleList = res.data
        }
      })
    },
    //   提交授权
    grantRoleSubmit () {
      let arr = this.$refs.tree.getCheckedNodes()
      console.log(arr)
      let rarr = arr.map((item) => { return item.id + ',' + item.pid })
      let tarr = rarr.join(',').split(',')
      let rids = [...new Set(tarr)]
      console.log(rids)
      console.log(typeof rids)
      grantRoleRight(this.currentRow.id, {rids: rids.join(',')})
        .then(res => {
        //   console.log(res)
          if (res.meta.status === 200) {
            this.$message({
              type: 'success',
              message: res.meta.msg
            })
            this.dialogFormVisible = false
            this.initList()
          }
        })
    },
    //   这里传递row(当前行)过来，目的是为了数据删除之前能够刷新当前行的数据
    deleteRoleRight (row, rightId) {
    //   console.log(row.id, rightId)
      deleteRight({roleId: row.id, rightId: rightId})
        .then(res => {
          if (res.meta.status === 200) {
            // this.roleList = res.data
            row.children = res.data
            // console.log(res.data)
          } else {
            this.$message({
              type: 'error',
              message: res.meta.msg
            })
          }
        })
    //   deleteRight()
    },
    // 打开分配角色对话框
    showGrantDialog (row) {
      this.dialogFormVisible = true
      this.currentRow = row
      getRightsList({type: 'tree'})
        .then(res => {
          if (res.meta.status === 200) {
            // console.log(res.data)
            this.rolesList = res.data
          } else {
            this.$message({
              type: 'error',
              message: res.meta.msg
            })
          }
        })
        // 遍历之前先让数组清空
      this.selectedRightId.length = 0
      // 取出当前所点击的角色的的所有权限，遍历它的第三个children,取出里面的id,这个id就是对应的权限
      this.currentRow.children.forEach(first => {
        if (first.children && first.children.length > 0) {
          first.children.forEach(second => {
            if (second.children && second.children.length > 0) {
              second.children.forEach(third => {
                this.selectedRightId.push(third.id)
              })
            }
          })
        }
      })
    }
  }
}
</script>
<style lang="scss" scoped>
    .roleContainer{
        height: 300px;
        overflow: auto;
    }
</style>



